import * as React from 'react';
import { Col, Row } from 'reactstrap';
import { Card, CardText, CardBody } from 'reactstrap';
import { Spinner } from 'reactstrap';
import { ResponsiveContainer, XAxis, YAxis, CartesianGrid, Legend, AreaChart, Area } from 'recharts';
import { Range, RemoveDuplicates } from '../../../../utils/arrayUtils';
import { Average, ProbabilityDensityFunction, StandardDeviation, Variance } from '../../../../utils/mathUtils';
import { Round } from '../../../../utils/numberUtils';
import { GetPlotColors } from '../../../../utils/plotUtils';
import { SecToString } from '../../../../utils/timeUtils';
import { ISolderingData } from './data/ISolderingData';

type ProcessOptimizationProps = {
    isLoading: boolean,
    detectedSolderings: ISolderingData[],
    fasterSoldering: ISolderingData
}

class ProcessOptimizationStep_CartridgeResult extends React.PureComponent<ProcessOptimizationProps> {
    public render() {
        const getPlot = (solderings: ISolderingData[]) => {

            let vals = solderings.map(x => x.solderingResult.tenthsOfSecondSoldering);
            let minVal = Math.max(Math.min(...vals) - 20, 0);
            let maxVal = Math.max(...vals) + 20;
            let range: number[] = Range(minVal, maxVal);

            let data : {[key:string]:number}[] = range.map(x => {
                return { "time": x };
            });

            let cartridges = solderings.map(x => x.cartridge);
            cartridges = RemoveDuplicates(cartridges);

            for (var i = 0; i < cartridges.length; i++) {
                let cartridge = cartridges[i];
                let valuesCart = solderings
                    .filter(x => x.cartridge === cartridge)
                    .map(x => x.solderingResult.tenthsOfSecondSoldering);

                let avg = Average(valuesCart);
                let variance = Variance(valuesCart);

                data = data.map(x => {
                    x[cartridge] = ProbabilityDensityFunction(x.time, avg, variance);
                    return x;
                });
            };

            let colors = GetPlotColors();
            let areas = cartridges.map((x, index) => {
                let color = colors[index % colors.length];
                return <Area type='monotone' dataKey={x} stroke={color["stroke"]} fill={color["fill"]} fillOpacity={0.3} />
            });

            return (
                <ResponsiveContainer width='100%' aspect={2.5}>
                    <AreaChart
                        data={data}
                        margin={{
                            left: -61,
                            right: 0
                        }}
                    >
                        <CartesianGrid stroke="#e6e6e6" />
                        <Legend/>

                        <XAxis
                            dataKey="time"
                            height={50}
                            label={"Time (s)"}
                            tickFormatter={(tick) => SecToString(Math.floor(tick / 10))}
                            interval={Math.floor(data.length / 5)} />
                        <YAxis/>

                        {areas}
                    </AreaChart>
                </ResponsiveContainer>
            );
        }

        const getPlotCard = () => {
            let cardBody;
            if (this.props.isLoading) {
                cardBody =
                    <div className="text-center">
                        <Spinner color="primary"/>
                    </div>;
            }
            else {
                cardBody =
                    <React.Fragment>
                        <CardText>Best cartridge: <strong>{this.props.fasterSoldering.cartridge}</strong></CardText>
                        {getPlot(this.props.detectedSolderings)}
                    </React.Fragment>
            }

            return (
                <Card>
                    <CardBody>
                        {cardBody}
                    </CardBody>
                </Card>
            )
        }

        const getTableCard = () => {
            let cardBody;
            if (this.props.isLoading) {
                cardBody =
                    <div className="text-center">
                        <Spinner color="primary"/>
                    </div>;
            }
            else {
                let cartridges = this.props.detectedSolderings.map(x => x.cartridge);
                cartridges = RemoveDuplicates(cartridges);

                let options =
                    cartridges.map(cartridge => {
                        let values = this.props.detectedSolderings
                            .filter(x => x.cartridge === cartridge)
                            .map(x => x.solderingResult.tenthsOfSecondSoldering);
        
                        let avg = Round(Average(values) / 10);
                        let std = Round(StandardDeviation(values) / 10);

                        return (
                            <tr className={this.props.fasterSoldering.cartridge === cartridge ? "font-weight-bold" : ""}>
                                <td>{cartridge}</td>
                                <td>{avg}</td>
                                <td>{std}</td>
                            </tr>)
                    })

                cardBody =
                    <table className="table table-sm">
                    <thead>
                        <tr>
                            <th scope="col">Cartridge</th>
                            <th scope="col">Time average (s)</th>
                            <th scope="col">Time desviation (s)</th>
                        </tr>
                    </thead>
                    <tbody>
                        {options}
                    </tbody>
                </table>
            }

            return (
                <Card>
                    <CardBody>
                        {cardBody}
                    </CardBody>
                </Card>
            )
        }

        return (
            <React.Fragment>
                <Row>
                    <Col xs="6" className="pl-0 pr-0">
                        {getPlotCard()}
                    </Col>
                    <Col  xs="6">
                        {getTableCard()}
                    </Col>
                </Row>
            </React.Fragment>
        );
    }
};

export default ProcessOptimizationStep_CartridgeResult;
